import { Module } from "vuex";
import { IDashboardState } from "./types";
import { IRootState } from "./../../types";
import {
  getGoodsCategoryCount,
  getGoodsAddressSale,
  getGoodsAmountList,
  getGoodsCategoryFavor,
  getGoodsCategorySale,
  getGoodsSaleTop10,
} from "@/service/main/analysis/dashboard";
const dashboardModule: Module<IDashboardState, IRootState> = {
  namespaced: true,
  state() {
    return {
      goodsCategoryCount: [],
      goodsCategorySale: [],
      goodsCategoryFavor: [],
      goodsSaleTop10: [],
      goodsAddressSale: [],
      goodsAmountList: [],
    };
  },
  mutations: {
    changeGoodsCategoryCount(state, list) {
      state.goodsCategoryCount = list;
    },
    changeGoodsCategorySale(state, list) {
      state.goodsCategorySale = list;
    },
    changeGoodsCategoryFavor(state, list) {
      state.goodsCategoryFavor = list;
    },
    changeGoodsSaleTop10(state, list) {
      state.goodsSaleTop10 = list;
    },
    changeGoodsAddressSale(state, list) {
      state.goodsAddressSale = list;
    },
    changeGoodsAmountList(state, list) {
      state.goodsAmountList = list;
    },
  },
  actions: {
    async getDashboardDataAction({ commit }) {
      const categoryCountResult = await getGoodsCategoryCount();
      commit("changeGoodsCategoryCount", categoryCountResult.data);

      const categorySaleResult = await getGoodsCategorySale();
      commit("changeGoodsCategorySale", categorySaleResult.data);

      const categoryFavorResult = await getGoodsCategoryFavor();
      commit("changeGoodsCategoryFavor", categoryFavorResult.data);

      const saleTop10Result = await getGoodsSaleTop10();
      commit("changeGoodsSaleTop10", saleTop10Result.data);

      const addressSaleResult = await getGoodsAddressSale();
      commit("changeGoodsAddressSale", addressSaleResult.data);

      const amountListResult = await getGoodsAmountList();
      commit("changeGoodsAmountList", amountListResult.data);
    },
  },
};

export default dashboardModule;
